/* ---------------------------
  1. GENERAL STYLING
  --------------------------*/

html {
  overflow: visible !important;
  margin: 0 !important;
}

body {
  background: #9d9d9d;
  overflow: hidden;
}

/* Body state */

body.loaded {
  overflow-x: hidden !important;
}

body.showMenu {
  overflow: hidden !important;
}

body.fp-responsive .section {
  padding-top: 60px;
  padding-bottom: 60px;
}

/* Helpers */

@media (max-width: 767px) {
  .text-center-xs {
    text-align: center;
  }
}

.inline-list {
  list-style-type: none;
  margin: 40px 0;
  padding: 0;
  cursor: default;
}

.inline-list > li {
  margin: 0 15px 0 0;
  display: inline-block;
}

.inline-list > li:last-of-type {
  margin-right: 0 !important;
}

/* Background */

#bg {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: url("../img/bg.jpg") no-repeat 50% 50%;
  background-size: cover;
  z-index: -3;
  filter: grayscale(1);
}

/* Fullpage.js Navigation */

@media (max-width: 767px) {
  #fp-nav {
    display: none !important;
  }
}

#fp-nav ul > li {
  margin: 10px 7px;
}

#fp-nav ul > li > a {
  position: relative;
}

#fp-nav ul > li > a:before {
  content: 'link';
  position: absolute;
  top: -5px;
  right: 0;
  color: white;
  text-decoration: none;
  opacity: 0;
  transition: all .5s ease;
}

#fp-nav ul > li > a.active:before {
  right: 35px !important;
  opacity: 1;
}

#fp-nav ul > li:hover > a:before {
  right: 25px;
  opacity: 1;
}

#fp-nav ul > li a[href^="#home"]:before {
  content: 'home';
}

#fp-nav ul > li a[href^="#about"]:before {
  content: 'about';
}

#fp-nav ul > li a[href^="#services"]:before {
  content: 'services';
}

#fp-nav ul > li a[href^="#portfolio"]:before {
  content: 'portfolio';
}

#fp-nav ul > li a[href^="#contact"]:before {
  content: 'contact';
}

/* Full page navigation normal state */

#fp-nav ul li a span {
  background-color: #fff !important;
  height: 2px !important;
  border-radius: 0 !important;
  left: 0 !important;
  margin: -2px 0 0 -2px !important;
  width: 6px;
}

/* Full page navigation hover state */

#fp-nav ul li:hover a span {
  width: 12px;
  left: -6px !important;
}

/* Full page navigation active state */

#fp-nav ul li a.active span {
  width: 24px !important;
  left: -14px !important;
}

/*  Animation */

.anim-container {
  position: relative;
  overflow: hidden;
  display: inline-block;
}

.anim-container .anim-mask {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 100%;
  background-color: #000;
}

.anim-container.animm .anim-mask {
  right: -1px;
  left: 100%;
}

.anim-container.animm .slide-anim {
  opacity: 1 !important;
}

/* ---------------------------
  2. PRELOADER
  --------------------------*/

.preloader-container {
  position: fixed;
  float: left;
  top: 0;
  width: 100%;
  height: 100%;
  bottom: 0;
  background-color: #000;
  z-index: 999999;
  transition: all 0.4s cubic-bezier(0.77, 0, 0.175, 1);
}

.preloader-container.preloader-container-left {
  margin-left: 100%;
}

.preloader-container .loader {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 20px;
  background: #fff;
  margin-left: -200px;
}

.preloader-container .loader.loader-anim-start {
  width: 400px;
  transition: all 0.6s cubic-bezier(0.77, 0, 0.175, 1);
}

.preloader-container .loader.loader-anim-end {
  width: 0;
  margin-left: 200px;
  transition: all 0.4s cubic-bezier(0.77, 0, 0.175, 1) 0.4s;
}

/* ---------------------------
  3. TYPOGRAPHY
  --------------------------*/

/* Typography */

body {
  font-family: 'Lato', sans-serif;
}

h1 {
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  font-size: 46px;
  color: #fff;
  line-height: 78px;
}

h1 span {
  background-color: #fff;
  color: #000;
  padding-left: 20px;
  padding-right: 20px;
}

h2 {
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  font-size: 36px;
  color: #fff;
}

h2 span {
  background-color: #fff;
  color: #000;
  padding-left: 20px;
  padding-right: 20px;
}

h3 {
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  font-size: 28px;
  color: #fff;
  line-height: 48px;
  margin-top: 40px;
  margin-bottom: 30px;
}

h3 span {
  background-color: #fff;
  color: #000;
  padding-left: 5px;
  padding-right: 5px;
}

p {
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  font-size: 18px;
  color: #fff;
}

p span {
  padding-left: 5px;
  padding-right: 5px;
  background-color: #fff;
  color: #000;
}

a {
  text-decoration: none;
  outline: 0;
  color: #fff;
}

a:hover,
a:focus,
a:active,
a.is-active,
a.active {
  text-decoration: none;
  outline: 0;
  color: #fff;
}

/* Selection style */

::selection {
  color: #000;
  background: #fff;
}

/* hr */

hr {
  width: 70px;
  border-top: 7px solid #fff;
}

/* ---------------------------
  4. BUTTONS
  --------------------------*/

.btn-black {
  position: relative;
  display: inline-block;
  padding: 15px 30px;
  border: 2px solid #fff;
  color: #fff;
  background: transparent;
  text-transform: uppercase;
  font-family: 'Lato', sans-serif;
  font-weight: 900;
  letter-spacing: .3em;
  font-size: 1.1rem;
  overflow: hidden;
  transition: all .5s ease;
  text-decoration: none;
  outline: 0;
}

.btn-black:hover,
.btn-black:focus,
.btn-black:active,
.btn-black.is-active,
.btn-black.active {
  text-decoration: none !important;
  outline: 0 !important;
}

.btn-black:hover,
.btn-black:active {
  background-color: #fff;
  color: #000;
}

.btn-black.invert {
  background-color: #fff;
  color: #000;
}

.btn-black.invert:hover,
.btn-black.invert:active {
  background: transparent;
  color: #fff;
}

.button-block {
  margin-top: 40px;
}

.button-block a.btn-black {
  margin-right: 30px;
}

.button-block a.btn-black:last-of-type {
  margin-right: 0 !important;
}

/* ---------------------------
  5. FORM
  --------------------------*/

input,
textarea {
  padding: 15px 20px;
  border: 2px solid #f2f2f2;
  border-radius: 2px;
  transition: all .3s ease;
  color: #fff;
  background-color: transparent;
  width: 100%;
  margin-bottom: 20px;
  outline: 0;
  font-family: 'Lato', sans-serif;
  font-weight: 300;
  font-size: 16px;
}

input:active,
input:focus,
input:hover,
textarea:active,
textarea:focus,
textarea:hover {
  outline: none;
}

/* ---------------------------
  6. POPUP
  --------------------------*/

/* Popup zoom animation */

.mfp-zoom-in .mfp-with-anim {
  opacity: 0;
  transition: all 0.2s ease-in-out;
  transform: scale(0.8);
}

.mfp-zoom-in.mfp-bg {
  opacity: 0;
  transition: all 0.3s ease-out;
}

.mfp-zoom-in.mfp-ready .mfp-with-anim {
  opacity: 1;
  transform: scale(1);
}

.mfp-zoom-in.mfp-ready.mfp-bg {
  opacity: 0.8;
}

.mfp-zoom-in.mfp-removing .mfp-with-anim {
  transform: scale(0.8);
  opacity: 0;
}

.mfp-zoom-in.mfp-removing.mfp-bg {
  opacity: 0;
}

/* Close cursor */

.mfp-zoom-out-cur,
.mfp-zoom-out-cur,
.mfp-zoom-out-cur .mfp-image-holder .mfp-close {
  cursor: url(../img/zoom-out-cur.png) 25 25, zoom-out;
}

/* ---------------------------
  7. MENU
  --------------------------*/

/* Menu */

.menu {
  position: fixed;
  width: 100%;
  height: 100%;
  left: -100%;
  top: 0;
  background: #000;
  z-index: 98;
  overflow-y: auto;
  overflow-x: hidden;
  transition: all 0.4s cubic-bezier(0.77, 0, 0.175, 1);
}

.menu.show {
  left: 0;
}

.menu .menu-links-ul {
  margin-top: 60px;
  margin-left: 60px;
  list-style: none;
}

@media (max-width: 767px) {
  .menu .menu-links-ul {
    margin-left: 10px;
  }
}

.menu .menu-links-ul .menu-links-li {
  margin: 20px;
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  font-size: 46px;
  color: #fff;
  position: relative;
  float: left;
  clear: both;
}

@media (max-width: 767px) {
  .menu .menu-links-ul .menu-links-li {
    font-size: 36px;
  }
}

@media (max-width: 479px) {
  .menu .menu-links-ul .menu-links-li {
    font-size: 26px;
  }
}

.menu .menu-links-ul .menu-links-li:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 100%;
  bottom: -40px;
  background-color: #fff;
  transform: translate3d(-20px, -20px, 0px);
  transition: all .5s ease;
}

.menu .menu-links-ul .menu-links-li:hover:before {
  right: -50px;
  transition: right 0.3s cubic-bezier(0.77, 0, 0.175, 1);
}

.menu .menu-links-ul .menu-links-li:hover .menu-links-a {
  display: inline-block;
  position: relative;
  color: #0d0d0d;
}

.menu .menu-links-ul .menu-links-li .menu-links-a {
  transition: color 0.2s ease-in-out;
}

/* Menu overlay */

.menu-mask {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  background: #fff;
  width: 0;
  height: 100%;
  transition: all 0.4s cubic-bezier(0.77, 0, 0.175, 1);
}

.menu-mask.mask-center {
  left: 0;
  width: 100%;
}

.menu-mask.mask-right {
  left: 100%;
  width: 0;
}

/* Show menu button */

.show-menu-button {
  position: fixed;
  z-index: 100;
  top: 25px;
  right: 25px;
  width: 50px;
  height: 50px;
  cursor: pointer;
}

.show-menu-button .lines {
  display: block;
  top: 50%;
  left: 10px;
  margin-top: -2px;
  width: 30px;
  height: 2px;
  background-color: #fff;
  border-radius: 4px;
  position: absolute;
  transition: all .5s ease;
}

.show-menu-button .lines::before,
.show-menu-button .lines::after {
  height: 2px;
  background-color: #fff;
  border-radius: 4px;
  position: absolute;
  transition: all .5s ease;
}

.show-menu-button .lines::before,
.show-menu-button .lines::after {
  content: "";
  display: block;
}

.show-menu-button .lines::before {
  top: -9px;
  width: 26px;
  margin-left: 4px;
}

.show-menu-button .lines::after {
  bottom: -9px;
  width: 20px;
  margin-left: 10px;
}

.show-menu-button:hover .lines::before,
.show-menu-button:hover .lines::after {
  width: 30px;
  margin-left: 0;
}

.show-menu-button.active .lines {
  transform: rotate(225deg);
  transition-delay: 0.12s;
}

.show-menu-button.active .lines::before {
  top: 0;
  opacity: 0;
  width: 30px;
  margin-left: 0;
}

.show-menu-button.active .lines::after {
  bottom: 0;
  width: 30px;
  margin-left: 0;
  transform: rotate(-90deg);
}

