/* About section */

.section-about .person-img img {
  filter: grayscale(1);
  width: 100%;
  height: auto;
}

.section-about .person-about {
  margin-top: 20px;
}

@media (max-width: 767px) {
  .section-about .person-about {
    margin: auto;
  }
}

.section-about .person-about .person-about-text {
  margin-top: 40px;
}

.section-about .person-about ul.personal-about-socials span {
  padding-left: 5px;
  padding-right: 5px;
  background-color: #fff;
  color: #000;
}

.section-about .person-about ul.personal-about-socials a {
  font-size: 28px;
}

/* Services section */

.section-services .service-cards {
  margin-top: 80px;
}

.section-services .service-cards .service-icon {
  width: 128px;
  height: 128px;
}

.section-services .service-cards .service-header {
  margin-top: 40px;
  margin-bottom: 25px;
}

/* Portfolio section */

.section-portfolio .inline-list {
  margin: 10px 0 0 0;
}

.section-portfolio .portfolio-filters ul > li.portfolio-filter {
  position: relative;
  display: inline-block;
  padding: 10px 25px;
  border: 2px solid #fff;
  color: #fff;
  text-transform: uppercase;
  font-family: 'Lato', sans-serif;
  font-weight: 900;
  letter-spacing: .3em;
  font-size: 1.1rem;
  overflow: hidden;
  transition: all .5s ease;
  text-decoration: none;
  outline: 0;
  cursor: pointer;
  margin-top: 5px;
  margin-bottom: 5px;
}

.section-portfolio .portfolio-filters ul > li.portfolio-filter:hover,
.section-portfolio .portfolio-filters ul > li.portfolio-filter.active {
  background-color: #fff;
  color: #000;
}

.section-portfolio .portfolio-grid {
  margin-top: 40px;
  margin-left: auto;
  margin-right: auto;
}

.section-portfolio .grid-item {
  width: 285px;
  height: 185px;
  margin-bottom: 5px;
  overflow: hidden;
  float: left;
  position: relative;
}

.section-portfolio .grid-item .portfolio-image:before {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #000;
  z-index: 10;
  opacity: 0;
  transition: opacity 0.5s cubic-bezier(0.77, 0, 0.175, 1);
}

.section-portfolio .grid-item .portfolio-image:after {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 11;
  opacity: 0;
  background: url("../img/plus.png") no-repeat center center;
  transition: opacity 0.5s cubic-bezier(0.77, 0, 0.175, 1) 0.1s;
}

.section-portfolio .grid-item .portfolio-image:hover:before,
.section-portfolio .grid-item .portfolio-image:hover:after {
  opacity: .9;
}

.section-portfolio .grid-item .portfolio-image img {
  width: 285px;
  height: 185px;
  transition: all 1s ease;
  filter: grayscale(1);
  opacity: 0.9;
  cursor: pointer;
  position: relative;
}

/* Contact section */

